


































export default {
	data() {
		return {
			status: true,
			arr:''
		};
	},
	onLoad() {
		this.getInfo()
	},
	methods: {
		setStatus(name) {
			var that = this;
			if (name == 'txz') {
				that.status = true
				that.getInfo()
			} else if (name == 'ytx') {
				that.status = false
				that.getInfo()
			}
		},
		getInfo() {
			var that = this;
			uni.showLoading({
				title:'数据加载中...'
			})
			uni.request({
				url: that.http + 'user/getUserTxlog',
				header: that.header,
				method: 'POST',
				data: {
					id: uni.getStorageSync('token'),
					type:2,
					status:that.status?'0':'1'
				},
				success: (res) => {
					uni.hideLoading()
					that.arr = res.data.data
				},
				fail() {
					uni.showToast({
						title:'网络延迟，请刷新后重试!'
					})
				}
			})
		}
	}
}
